@charset "utf-8";
/* --------------------------------------------------------------
 * = 全局颜色
 * ----------------------------------------------------------- */
/* --------------------------------------------------------------
* = 样式重置
* ----------------------------------------------------------- */
/* --------------------------------------------------------------
* = 通用样式
* ----------------------------------------------------------- */
.sep {
  margin-bottom: 15px;
}
.pure-button {
  margin-right: 5px;
}
.offside ul {
  padding: 30px;
}
.offside .col {
  padding: 0;
}
.offside .col ul {
  padding: 0;
}
/* --------------------------------------------------------------
* = 整体布局, 很多页面共享的页面框架
* ----------------------------------------------------------- */
@media screen and (min-width: 48em) {
  .site {
    position: relative;
    overflow: hidden;
  }
}
.site-main {
  margin-top: 30px;
  padding: 0 30px 0 0;
}
@media screen and (max-width: 48em) {
  .site-main {
    margin-top: 15px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.page-header {
  border: 3px solid #ddd;
  padding: 15px;
  margin-bottom: 30px;
}
.page-header .page-title {
  color: #168BDE;
  margin-bottom: 15px;
}
.page-header .taxonomy-description p {
  margin-bottom: 0;
}
/* --------------------------------------------------------------
* = 页头, 网站顶部内容样式
* ----------------------------------------------------------- */
.site-header {
  background: #168BDE;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  color: #fff;
  margin-bottom: 15px;
}
.site-header h1,
.site-header h3 {
  color: #fff;
}
.site-title a {
  color: #fff;
  font-size: 18px;
}
@media screen and (max-width: 48em) {
  .site-title {
    height: 44px;
    line-height: 44px;
  }
  .site-title a {
    line-height: 44px;
  }
}
/* --------------------------------------------------------------
* = 文章页排版
* ----------------------------------------------------------- */
.hentry h2 {
  border-bottom: 1px dotted #ddd;
  color: #168BDE;
  font-size: 18px;
  padding-bottom: 6px;
}
.hentry h3 {
  color: #666;
  font-size: 16px;
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
}
.entry-header {
  margin-bottom: 30px;
  padding-bottom: 10px;
  border-bottom: 3px solid #ddd;
}
.entry-content p {
  margin-bottom: 1em;
}
.entry-footer .entry-tags {
  margin-bottom: 10px;
}
.single .entry-content {
  margin-bottom: 30px;
}
/* --------------------------------------------------------------
* = 首页
* ----------------------------------------------------------- */
/* --------------------------------------------------------------
* = 存档页
* ----------------------------------------------------------- */
.archive .site-main {
  padding: 0 30px 0 0;
  background: transparent;
}
@media screen and (max-width: 48em) {
  .archive .site-main {
    padding: 15px;
  }
}
.archive .site-main .media {
  padding: 30px 0;
}
.archive .site-main .media + .media {
  margin-top: 0;
}
.archive .hentry {
  background: #fff;
  margin-bottom: 30px;
  border-bottom: 1px solid #ddd;
}
.archive .entry-content {
  padding: 0 30px 30px;
  position: relative;
}
.archive .entry-content h2 {
  border: 0;
  margin-top: 0;
  font-size: 1.4em;
}
/* --------------------------------------------------------------
* = 边栏
* ----------------------------------------------------------- */
.widget-area {
  padding-top: 30px;
}
.widget-area > .col {
  padding-left: 30px;
}
.widget {
  margin-bottom: 50px;
}
.widget a {
  color: #000;
}
.widget a:hover {
  color: #168BDE;
}
.widget-title {
  margin-bottom: 10px;
  color: #333;
}
.widget_categories ul {
  border-bottom: 1px solid ccc;
  border-radius: 3px ;
}
/* --------------------------------------------------------------
* = 页脚
* ----------------------------------------------------------- */
.site-footer {
  border-top: 1px solid #eee;
  padding: 1.5em;
  margin-top: 30px;
}
